* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}


:root {
  --primary-color: #e8e8e8;
  --dropdown-hover: #f5ebe0;
  --text-color-1: #20201d;
  --text-color-2: #fff;
  --text-color-1-hover: #4D869C;
  --border-input: #20201d;
  --border-input-focus: #4D869C;
  --button-color: #4D869C;
  --button-color-hover: #2496c4;
  --form-control-border: #cccc;
  --scrollbar-color: #4D869C;
  --panel-color: #eaeaea;
}

body {
  background-color: var(--primary-color);
}

/* ============= TOATS ============= */

.toast-success {
  background-color: #5a58a4 !important;
}

.toast-warning {
  background-color: #f0cd81 !important;
}

.toast-error {
  background-color: #ff537e !important;
}

/* ============= BUTTON =========== */
.button {
  padding: 0.8rem 1rem;
  font-size: 0.9rem;
  color: var(--text-color-2);
  background-color: var(--button-color);
  border: none;
  outline: none;
  border-radius: 0.2rem;
  cursor: pointer;
  transition: all 0.2s;
}

.button:hover {
  background-color: var(--button-color-hover);
}

.button-icon {
  background-color: var(--button-color);
  border-radius: 0.2rem;
  border: none;
  outline: none;
  padding: 0.6rem 0.7rem;
  cursor: pointer;
  transition: all 0.2s;
}

.button-icon:hover {
  background-color: var(--button-color-hover);
}

.button-icon i {
  font-size: 1.5rem;
  color: var(--text-color-2);
}

.button-close {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 0 0.5rem 0 0.5rem;
  padding: 0.6rem 1rem;
}

/* ============= SEARCH =========== */

.search-title {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.search-title h2.title {
  font-size: 2rem;
  color: var(--button-color);
  margin-bottom: 0;
}

.search-title .search-button-back {
  background: transparent;
  border: none;
  outline: none;
  padding: 0.5rem 0.6rem;
  cursor: pointer;
}

.search-title .search-button-back i {
  font-size: 1.8rem;
  color: var(--button-color);
}

/* =========== LOADER ===========  */

.loader-box {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.loader {
  position: absolute;
  left: 135px;
  display: none;
  align-items: center;
  transition: opacity 0.75s, visibility 0.75s;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader::after {
  content: '';
  width: 16px;
  height: 16px;
  border: 6px solid #dddddd;
  border-top-color: var(--scrollbar-color);
  border-radius: 50%;
  animation: loading 0.75s ease infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}

/* ============= NAVBAR =========== */
header {
  position: sticky;
  top: 0;
  z-index: 10;
  padding: 1rem 1rem;
  background-color: #CDE8E5;
}

nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.logo,
.nav-menu a,
.dropdown-menu a {
  text-decoration: none;
}

.nav-menu a,
.dropdown-menu a {
  text-decoration: none;
  padding: 0.7rem 0;
}

.logo {
  width: 8rem;
  padding: 0.4rem 0;
}

.hamburger-menu {
  padding: 0.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.hamburger-menu i {
  color: var(--button-color);
}

.nav-menu {
  display: none;
  gap: 1rem;
}

.nav-menu a {
  font-size: 1rem;
  color: var(--text-color-1);
  transition: all 0.2s;
}

.nav-menu a:hover {
  color: var(--text-color-1-hover);
}

.form-search {
  display: none;
}

.form-search {
  justify-content: end;
  gap: 0.5rem;
  width: 100%;
}

.form-control {
  width: 100%;
  padding: 0.8rem 1rem;
  font-size: 0.9rem;
  border-radius: 0.2rem;
  border: 1px solid var(--form-control-border);
  outline: none;
}

.form-control:focus {
  border-color: var(--border-input-focus);
  box-shadow: 0 0 5px rgba(183, 150, 92, 0.5);
}

.button-search {
  padding: 0.8rem 1rem;
  font-size: 0.9rem;
  color: var(--text-color-2);
  background-color: var(--button-color);
  border: none;
  outline: none;
  border-radius: 0.2rem;
  cursor: pointer;
  transition: all 0.2s;
}

.button-search:hover {
  background-color: var(--button-color-hover);
}

.dropdown-menu {
  position: absolute;
  padding: 2rem 1rem;
  top: 1000px;
  left: 1rem;
  right: 1rem;
  background-color: var(--button-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 0.5rem;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.3s ease, max-height 0.3s ease;
}

.dropdown-menu.active {
  top: 95px;
  opacity: 1;
  max-height: 500px;
}

.dropdown-menu a {
  color: var(--text-color-2);
  transition: all 0.2s;
}

.dropdown-menu a:hover {
  color: var(--dropdown-hover);
}

.form-dropdown {
  display: flex;
}

.button-dropdown {
  background-color: var(--text-color-2);
  color: var(--button-color);
}

.button-dropdown:hover {
  background-color: var(--dropdown-hover);
}

/* ============= MAIN ============= */

.main {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 84px);
}

main {
  flex: 1;
}

/* ============= JUMBOTRON ============= */

.jumbotron {
  width: 100%;
  height: 80vh;
  background-size: cover;
  background-position: center;
}

.jumbotron-overlay {
  padding: 1rem 1rem;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.jumbotron-title {
  color: var(--text-color-2);
  text-align: center;
  font-size: 3.5rem;
}

.jumbotron-subtitle {
  color: var(--text-color-2);
  text-align: center;
  font-size: 1rem;
}

.restaurant-list {
  padding: 3rem 1rem;
  width: 100%;
}

.restaurant-list h2 {
  text-align: center;
  color: var(--button-color);
  margin-bottom: 2rem;
  font-size: 2rem;
}

.cards-box {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}

.cards-box .error {
  font-size: 1.2rem;
  color: var(--text-color-1);
  font-style: italic;
  font-weight: 300;
}

a.card {
  height: fit-content;
  position: relative;
  width: 100%;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  text-decoration: none;
  color: var(--text-color-1);
}

.card img {
  width: 100%;
  height: 12rem;
  object-fit: cover;
  border-radius: 0.2rem;
}

.city {
  text-align: center;
  top: 1rem;
  left: 1rem;
  border-radius: 0.2rem 0 0.2rem 0;
  background-color: var(--button-color);
  padding: 5px 10px 5px;
  color: white;
  font-weight: 500;
}

.card-rating {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0.3rem;
}

.card-rating i {
  color: var(--button-color);
  font-size: 1.2rem;
}

.card-rating a {
  text-decoration: none;
  font-size: 1rem;
  color: var(--text-color-1);
}

.card-title {
  color: var(--text-color-1);
  font-size: 1.5rem;
}

.card-desc {
  color: var(--text-color-1);
  font-size: 1rem;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.card-desc.active {
  display: unset;
  -webkit-line-clamp: unset;
  -webkit-box-orient: unset;
  overflow: unset;
}

.button-read-more {
  margin-top: 1rem;
}

/* ============= FOOTER ============= */

end-bar{
  background-color: #CDE8E5;
}

footer p {
  font-family: calibri;
}

footer p:nth-child(1) {
  font-size: 30px;
  font-weight: bold;
  color: var(--text-color-1);
  margin: 20px;
}

footer p:nth-child(2) {
  font-size: 16px;
  color: var(--text-color-1);
  width: 600px;
  text-align: center;
  margin: 5px;
}

footer {
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: #cde8e5;
  border-radius: 0.5rem;
}

.social-icons a {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #26434e;
  margin: 20px 10px;
  border-radius: 50%;
}

.social-icons {
  display: flex;
}

.social-icons i,
.social i {
  color: #fff
}

.social-icons a:hover {
  background-color: #fffffe;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.2);
  transition: all ease 0.5s;
}
.social-icons a:hover i,
.social a:hover i {
  color: #004643;
  transition: all ease 0.5s;
}

.copyright {
  color: var(--text-color-1);
  font-size: 15px;
}

.social {
  position: fixed;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
}

.social a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  margin: 0px;
  padding: 0px;
  line-height: 0px;
  background-color: #004643;
  border: 1px solid #cbcbcb;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.2);
}

.social a:hover {
  background-color: #abd1c6;
  color: #004643;
  transition: all ease 0.5s;
}

.social i {
  font-size: 20px;
  color: #fffffe;
}

.skip-content {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: var(--button-color);
  border: none;
  border-radius: 0 0 0.3rem 0;
  outline: none;
  text-decoration: none;
  color: white;
  padding: 8px;
  z-index: 20;
}

.skip-content:focus {
  top: 0;
}

/* ============= DETAIL =========== */

.detail-box {
  width: 100%;
  background-size: cover;
  background-position: center;
  background-color: #4D869C;
}

.detail-overlay {
  padding: 3rem 1rem;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  align-content: center;
}

.detail-button-favorit {
  top: 3.5rem;
  right: 1.5rem;
  display: flex;
  align-items:center;
  justify-content: flex-start;
  gap: 0.5rem;
  z-index: 2;
}

.detail-title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.detail-title .detail-button-back {
  padding: 0.5rem;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.detail-button-back i {
  color: var(--text-color-2);
  font-size: 2.2rem;
}

.detail-title h2 {
  font-size: 2.2rem;
  color: var(--text-color-2);
}

.detail-content h6 {
  padding-left: 54.8px;
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--text-color-2);
}

.detail-address-box {
  padding: 1rem;
  border-radius: 0.5rem;
  display: flex;
  gap: 1rem;
  color: #fff;
}

.detail-address {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.detail-address p {
  font-weight: 400;
}

.detail-address i {
  font-size: 1.2rem;
  color: var(--button-color);
}

hr {
  width: 2px;
  background-color: var(--button-color);
  border: none;
}

.detail-description {
  margin-top: 1rem;
  font-weight: 300;
  color: var(--text-color-2);
}

.detail-content-image {
  display: flex;
  align-items: center;
}

.detail-content-image img {
  width: 100%;
  border-radius: 0.5rem;
}

/* ============= MENU ============= */

.menu-box {
  margin-top: 3rem;
  padding: 0 1rem;
}

.menu-box h2 {
  text-align: center;
  color: var(--button-color);
  margin-bottom: 2rem;
  font-size: 2.2rem;
}

.menu-content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}

.menu-title {
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.menu-title h3 {
  font-size: 1.6rem;
  color: var(--scrollbar-color);
}

.menu-title i {
  font-size: 1.5rem;
  color: var(--scrollbar-color);
}

.menus-content p {
  font-size: 1rem;
  color: var(--text-color);
  line-height: 1.5rem;
}

.food {
  padding: 1rem;
  background-color: var(--primary-color);
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
}

/* ============= REVIEW =============*/

.review-box {
  padding: 3rem 1rem;
}

.review-box h2 {
  text-align: center;
  color: var(--button-color);
  margin-bottom: 2rem;
  font-size: 2.2rem;
}

.review-content {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  border-radius: 0.5rem;
  background-color: #26434e;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}


.review-overal h3 {
  font-size: 1.6rem;
  color: var(--scrollbar-color);
  color: #fff;
}

.review-rating {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #fff;
}

.review-rating h4 {
  font-size: 1.2rem;
  font-weight: 500;
  color: #fff;
}

#button-review-trigger{
background-color: var(--primary-color);
color: var(--button-color);
}
#button-review-trigger:hover{
background-color: var(--button-color);
color: var(--primary-color);
}

.review-rating i {
  font-size: 1rem;
  color: var(--button-color);
  color: #fff;
}

.review-rating p {
  font-size: 0.8rem;
  font-style: italic;
  font-weight: 400;
  color: #fff;
}

.review-body {
  max-height: 20rem;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  color: #4D869C;
  background-color: #e2e6e8;
  
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: var(--panel-color);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-color);
  border-radius: 5px;
}

.review-customer{
  margin-top: 1rem;
  border: 2px solid var(--button-color);
  border-radius: 1rem;
}

.review-customer-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.review-customer-header img {
  width: 2.3rem;
  margin: 0.5rem;
}

.review-name h4 {
  font-size: 1rem;
  font-weight: 600;
}

.review-name p {
  font-size: 0.8rem;
  font-weight: 500;
}

.review-customer-body{
  margin: 0.5rem;
}

/* ============= MODAL ============= */

.modal-box {
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 1rem;
  background-color: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 100vh;
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 12;
}

.modal-box.active {
  display: flex;
}

.modal-image {
  position: relative;
  width: 100%;
  animation-name: animatetop;
  animation-duration: 0.4s;
}

.modal-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0.5rem;
}

.modal-review {
  position: relative;
  width: 100%;
  background-color: var(--primary-color);
  padding: 1rem;
  border-radius: 0.5rem;
  animation-name: animatetop;
  animation-duration: 0.4s;
}

.modal-review h3 {
  margin-bottom: 1rem;
  font-size: 1.6rem;
  color: var(--button-color);
}

.form-review {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.button-review {
  width: 100%;
}

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
